<template>
  <div class="contact-form">
    <h2>Contact Me</h2>
    <form @submit.prevent="handleSubmit">
      <div class="form-group">
        <label for="name">Name:</label>
        <input id="name" v-model="form.name" type="text" required />
      </div>
      <div class="form-group">
        <label for="email">Email:</label>
        <input id="email" v-model="form.email" type="email" required />
      </div>
      <!-- Updated questions field to mimic Medium description -->
      <div class="form-group">
        <label for="description">Description:</label>
        <textarea id="description" v-model="form.message" required placeholder="Enter your detailed question or description..."></textarea>
      </div>
      <!-- New attachments input -->
      <div class="form-group">
        <label for="attachments">Attachments:</label>
        <input id="attachments" type="file" @change="handleFileChange" multiple />
      </div>
      <button type="submit">Submit</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "ContactMe",
  data() {
    return {
      form: {
        name: "",
        email: "",
        message: ""
      },
      attachments: [] // store selected files
    };
  },
  methods: {
    handleFileChange(e) {
      // Capture all selected files in attachments array
      this.attachments = Array.from(e.target.files);
    },
    handleSubmit() {
      console.log("Form submitted", this.form, this.attachments);
      alert("Thank you for contacting me!");
      // Reset form and attachments
      this.form.name = "";
      this.form.email = "";
      this.form.message = "";
      this.attachments = [];
      // Optionally clear file input here if needed using refs
    }
  }
}
</script>

